import React, { useState, useRef } from 'react'
import { graphql } from 'gatsby'
import PropTypes from 'prop-types'

import {
	faHome,
	faAnchor,
	faRunning,
	faFilter,
} from '@fortawesome/free-solid-svg-icons'

import { AppContext } from '@src-components/contexts'
import { SEO, Layout } from '@src-components/foundations'
import { ArticleContent, PageHeading } from '@src-components/parts'

const HookSingleTemplate = props => {
	const { location, data } = props

	const { body, tableOfContents, frontmatter } = data.mdx

	const [isOpenMenu, setIsOpenMenu] = useState(false)
	const [isOpenSearch, setIsOpenSearch] = useState(false)
	const [isOpenTOC, setIsOpenTOC] = useState(false)
	const activeTOC = useRef(null)
	const [isDarkMode, setIsDarkMode] = useState(null)

	const pageTitle = frontmatter.title
	const pageDescription = frontmatter.description

	const pageValues = {
		title: pageTitle,
		description: pageDescription,
		location: location.pathname,
		tocItems: tableOfContents.items,
		breadcrumbItems: [
			{ label: 'ホーム', url: '/', icon: faHome },
			{ label: 'フック', url: '/hooks/', icon: faAnchor },
			{
				label: frontmatter.title,
				url: location.pathname,
				icon: frontmatter.hookType === 'action' ? faRunning : faFilter,
			},
		],
		isOpenMenu: isOpenMenu,
		setIsOpenMenu: setIsOpenMenu,
		isOpenSearch: isOpenSearch,
		setIsOpenSearch: setIsOpenSearch,
		isOpenTOC: isOpenTOC,
		setIsOpenTOC: setIsOpenTOC,
		activeTOC: activeTOC,
		isDarkMode: isDarkMode,
		setIsDarkMode: setIsDarkMode,
	}

	return (
		<AppContext.Provider value={pageValues}>
			<SEO />
			<Layout>
				<PageHeading
					icon={frontmatter.hookType === 'action' ? faRunning : faFilter}
				>
					{pageTitle}
				</PageHeading>
				<div className="mt-8">
					<ArticleContent>{body}</ArticleContent>
				</div>
			</Layout>
		</AppContext.Provider>
	)
}

HookSingleTemplate.propTypes = {
	data: PropTypes.object,
	pageContext: PropTypes.object,
}

export default HookSingleTemplate

export const pageQuery = graphql`
	query($id: String!) {
		mdx(id: { eq: $id }) {
			id
			body
			tableOfContents
			frontmatter {
				hookType
				title
				description
				order
			}
			fields {
				slug
				name
			}
		}
	}
`
